.wallpaper{
    position:fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}
.icon3{
    position:fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height:25vw;
}
.bottom-logo{
    position:fixed;
    bottom: 16px;
    right: 32px;
    display: flex;
    gap: 34px;
}
.icon1, .icon2{
    object-fit: cover;
    height: 64px;
}